 body {
    margin: 0;
    background: #151616;
    font-family: roboto;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  
  code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
      monospace;
  }

  :root {
    --bg: #17a0b0;
    --bg-accent: #5c3a9fff;
    --text-color: #dadce1;
    --nav-size: 60px;
    --border: 1px solid #525357;
    --border-radius: 8px;
    --speed: 500ms;
  }
  
  ul {
    list-style: none;
    margin: 0;
    padding: 0;
  }
  
  a {
    color: #dadce1;
    color: var(--text-color);
    text-decoration: none;;
  }
